.header-nav {
  position: absolute;
  top: 2rem;
  right: 2rem;
  z-index: 10;
}

.header-nav ul {
  display: flex;
  gap: 1.5rem;
  list-style: none;
  font-family: var(--font-body);
  font-size: 0.95rem;
  font-weight: 500;
  color: var(--color-navy);
}

.header-nav li {
  cursor: pointer;
  transition: color 0.3s ease;
}

.header-nav li:hover {
  color: var(--color-accent);
}
